.research {
  display: none;
  margin-bottom: 72px;
  padding: 0;

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: calc((100% - 660px) / 2) repeat(2, 1fr) calc((100% - 660px) / 2);
    grid-template-rows: 70px 1fr;
    grid-template-areas:
      ". image . ."
      ". image research .";
  }

  @media (min-width: $desktop-width) {
    grid-template-rows: 95px 1fr;
    grid-template-columns: calc((100% - 960px) / 2) 1fr 2fr calc((100% - 960px) / 2);
    margin-bottom: 64px;
    background-color: $color-section-background;
  }
}

.research__wrapper {
  grid-area: research;

  @media (min-width: $tablet-width) {
    min-height: 276px;
    min-width: 310px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 61px;
    min-height: 345px;
    min-width: 530px;
  }
}

.research__image {
  grid-area: image;

  @media (min-width: $tablet-width) {
    background-image: url("../img/research-tablet.svg");
    background-position: 22% 68%;
    background-repeat: no-repeat;
  }

  @media (min-width: $desktop-width) {
    background-image: url("../img/research-desktop.svg");
    background-position: 50% 20%;
  }
}

.research__title {
  margin: 0;
  padding-top: 19px;
  color: $color-secondary-text;
  font-size: 30px;
  line-height: 36px;
  font-weight: 700;

  @media (min-width: $tablet-width) {
    max-width: 60%;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 23px;
    padding-top: 16px;
  }
}

.research__text {
  margin-bottom: 8px;

  @media (min-width: $desktop-width) {
    margin-bottom: 32px;
    font-size: 18px;
    line-height: 30px;
  }
}

.research__link {
  color: $link-color;
  text-decoration: underline;

  @media (min-width: $desktop-width) {
    font-size: 18px;
  }
}
